@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  min-height: 100vh;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  background-color: #efefef;
}

#root {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
} 

.App {
  width: 100%;
  max-width: 800px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #333;
  box-shadow: 0px 0px 15px gray;
}

.header, .footer {
  width: 100%;
  background-color: #66d8f5;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  font-size: 1.5rem;
}

.header svg {
  font-size: 2rem;
}

.footer {
  padding: 0.75rem;
  display: grid;
  place-content: center;
}

.nav {
  width: 100%;
  background-color: #333;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.searchForm {
  width: 80%;
  padding: 1rem 0 0 0.75rem;
}

.searchForm input[type="text"] {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  min-height: 48px;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  outline: none;
}

.searchForm label {
  position: absolute;
  left: -99999px;
}

.nav ul {
  color: #fff;
  list-style-type: none;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.nav li {
  padding: 1rem;
}

.nav li:hover, 
.nav li:focus {
  padding: 1rem;
}

.nav li a {
  color: #fff;
  text-decoration: none;
}

.nav li:hover, 
.nav li:focus, 
.nav li:hover a, 
.nav li:focus a  {
  background-color: #eee;
  color: #333;
}

.nav li:hover a, 
.nav li:focus a {
  cursor: pointer;
}

.home, .newPost, .postPage, .about, .missing {
  width: 100%;
  flex-grow: 1;
  padding: 1rem;
  overflow-y: auto;
  background-color: #fff;
}

.post {
  margin-top: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid lightgray;
}

.home .post a {
  text-decoration: none;
}

.home .post a, 
.home .post a:visited {
  color: #000;
}

.post:first-child {
  margin-top: 0;
}

.post:last-child {
  border-bottom: none;
}

.postDate {
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

.postBody {
  margin: 1rem 0;
}

.postForm {
  display: flex;
  flex-direction: column;
}

.postForm label {
  margin-top: 1rem;
}

.postForm input[type='text'], 
.postForm textarea {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  min-height: 48px;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  margin-right: 0.25rem;
  outline: none;
}

.postForm textarea {
  height: 100px;
}

.postForm button {
  margin-top: 1rem;
  height: 48px;
  min-width: 48px;
  border-radius: 10px;
  padding: 0.5rem;
  font-size: 1rem;
  cursor: pointer;
}

.missing p, .postPage p, .newPost p {
  margin-top: 1rem;
}

.postPage button {
  height: 48px;
  min-width: 48px;
  border-radius: 0.25rem;
  padding: 0.5rem;
  margin-right: 0.5rem;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
}

.deleteBtn {
  background-color: red;
}

.editBtn {
  background-color: #333;
}

.statusMsg {
  margin-top: 2rem;
}

.about {
  display: flex;
  flex-flow: column nowrap;
  gap: 1rem;
}

.about ul {
  margin-left: 1.125rem;
}

@media only screen and (min-width: 610px) {
  html {
    font-size: 22px;
  }

  .header h1 {
    font-size: 2rem;
  }

  .nav {
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
  }

  .nav ul {
    text-align: right;
  }

  .nav li:hover, 
  .nav li:focus, 
  .nav li:hover a, 
  .nav li:focus a  {
    background-color: #eee;
    color: #333;
  }

  .searchForm {
    width: 50%;
    padding: .5rem 0;
  }
 
  .searchForm input[type="text"] {
    margin-left: 0.5rem;
  }

  .postForm textarea {
    height: 300px;
  }
}

@media only screen and (min-width: 992px) {

  .header svg {
    font-size: 3rem;
  }

}
